nav {
	@include flex-pack (space-between, center, wrap, 70%);
	padding-top: 45px;
	@include r(1366) {
		flex-basis: 90%; }
	@include r(480) {
 } }		// justify-content: center

.logo {
	@include flex;
	flex-basis: 25%;
	@include r(768) {
		flex: 1 0 100%;
		justify-content: center;
		margin-bottom: 30px; }
	&_leaf {
		& img {
			width: 55px;
			height: 62px;
			@include r(1160) {
				width: 40px;
				height: 47px; } } } }

.logo_text {
	text-align: center;
	font-size: 1.1em;
	color: #8EBC5A;
	letter-spacing: 2px;
	@include r(1160) {
		font-size: 1em; }
	& img {
		width: 85%;
		@include r(1160) {
			width: 85%; } } }


nav > ul {
	@include flex-pack(space-between, false, wrap, 55%);
	list-style: none;
	@include r(1160) {
		flex-basis: 70%; }
	@include r(768) {
		flex: 1 0 100%; }
	@include r(480) {
		margin-top: 50px; }
	& li {
		@include r(480) {
			margin-bottom: 15px; } }
	@include r(480) {
		flex-direction: column;
		text-align: center; } }




nav a {
	font-size: 15px;
	text-decoration: none;
	font-weight: 100;
	@include r(900) {
		font-size: 1em; }
	@include r(556) {
		font-size: 0.7em; }
	@include r(480) {
		font-size: 1.1em; } }


