.map {
  margin-top: 0;
  padding: 20px 0;
  position: relative;
  background-image: url(../img/map.png);
}

.map::before {
  content: " ";
  display: block;
  position: absolute;
  background: #000;
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 2;
}

.map-form {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: -o-flex;
  display: flex;
  width: 75%;
  margin: 0 auto;
  position: relative;
  background: #fff;
  flex-wrap: nowrap;
  justify-content: space-around;
  z-index: 50;
}

@media only screen and (max-width: 600px) {
  .map-form {
    width: 90%;
    flex-wrap: wrap;
  }
}

.map-view {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: -o-flex;
  display: flex;
  flex-basis: 25%;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  align-items: center;
  margin: 7em 0;
}

