.map {
	margin-top: 0;
	padding: 20px 0;
	position: relative;
	background-image: url(../img/map.png); }

.map::before {
	content: " ";
	display: block;
	position: absolute;
	background: #000;
	opacity: 0.5;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	z-index: 2; }

.map-form {
	@include flex;
	width: 75%;
	margin: 0 auto;
	position: relative;
	background: #fff;
	flex-wrap: nowrap;
	justify-content: space-around;
	z-index: 50;
	@include r(600) {
		width: 90%;
		flex-wrap: wrap; } }

.map-view {
	@include flex;
	flex-basis: 25%;
	justify-content: center;
	flex-wrap: wrap;
	text-align: center;
	align-items: center;
	margin: 7em 0;
	@include r(1280) {
		flex-basis: 40%; }
	@include r(600) {
		flex-basis: 100%; } }

.map-view img {
	width: 35%; }

.map-view p {
	flex-basis: 100%;
	margin: 2em; }

.map-view button {
	flex-basis: 50%;
	color: #fff;
	background: #90D23D; }

.send-message {
	@include flex;
	flex-basis: 25%;
	flex-wrap: wrap;
	margin: 3em 0;
	align-items: space-around;
	justify-content: center;
	@include r(1280) {
		flex-basis: 40%; }
	@include r(600) {
		flex-basis: 90%; } }

.send-message input[type=email] {
	flex-basis: 100%;
	height: 5%;
	padding: 1em;
	font-size: 0.9em;
	border-radius: 5px;
	border: 1px solid #A1D95B;
	@include placeholder {
		padding: 5px; } }

.send-message textarea {
	flex-basis: 100%;
	height: 50%;
	font-size: 1em;
	padding: 0.7em;
	border: 1px solid #C8C8C8;
	border-radius: 5px;
	resize: none; }

.send-message input[type=submit] {
	flex-basis: 100%;
	height: 13%;
	font-size: 1em;
	border-radius: 5px;
	border: none;
	color: #fff;
	background: #373737;

 } // ------------------MAP PART END----------------

